@import '../../styles/mixins';

$large-size: 1320px;
$medium-size: 1024px;
$small-size: 768px;
$smallest-size: 480px;

$font-size-base: 14;
$line-heaight-base: 17px;
$font-family-base: 'Roboto', sans-serif;
$font-family-solid: 'Montserrat', sans-serif;
$font-family-header: 'Fugaz One', cursive;
$font-family-text: 'Open Sans', sans-serif;

$primary-1: #ae0032;
$primary-2: #455162;
$primary-3: #1f2f46;

$neutral-1: #0d1720;
$neutral-2: #636363;
$neutral-3: #dadada;
$neutral-4: #f2f2f2;
$neutral-5: #fcfbfa;

$gray-1: #F1F1F1;
$gray-2: #404040;
$gray-3: #292929;
$gray-4: #333C30;
$gray-5: #F8F8F8;
$gray-6: #D4D4D4;

$orange: #EB5B1F;
$orange-1:#B44619;
$orange-hover:#DC6331;

$green-1: #1B3A1B;

.banner {
  position: relative;
  background-image: url('images/bg-banner.svg');
  background-repeat: no-repeat;
  height: 677px;
  @include phone {
    background-image: url('images/bg-banner-m.svg');
    background-position: right;
    background-size: cover;
    overflow: hidden;
  }
}

.banner-block {
  padding-top: 120px;
  width: 66%;
  position: relative;
  @include phone {
    width: 100%;
    padding-top: 45px;
  }
}  

.banner-image {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
  height: auto;
  @include phone {
    right: -40%;
    height: 100%;
  }
  img {
    @include phone {
      position: absolute;
      top: auto;
      bottom: 0;
      right: 0;
      z-index: -1;
      height: 85%;
      width: auto;
      max-width: initial;
    }
  }
}

.banner-header,
.banner-text {
  color: white;
}

.banner-header {
  font-family: $font-family-header;
  font-size: 36px;
  line-height: 52px;
  text-transform: uppercase;
  margin-bottom: 16px;
  @include phone {
    font-size: 22px;
    line-height: 32px;
    margin-bottom: 10px;
  }
}

.banner-text {
  font-size: 16px;
  line-height: 26px;
  max-width: 70%;
  margin-bottom: 40px;
  @include phone {
    font-size: 14px;
    line-height: 24px;
    font-weight: 400;
    max-width: 77%;
    margin-bottom: 16px;
  }
}
.contacts-link,
.play-video-link {
  @include phone {
    display: block;
    width: 100%;
    text-align: center;
  }
}

.contacts-link {
  font-family: $font-family-header;
  color: white;
  background-color: $orange;
  padding-left: 56px;
  padding-right: 56px;
  border-color: $orange;
  &:hover {
    background-color: $orange-hover;
    border-color: $orange-hover;
  }
  &:focus {
    border-color: $gray-4;
  }
}

.play-video-link {
  font-family: $font-family-header;
  color: white;
  background-color: transparent;
  border-color: white;
  padding-left: 32px;
  padding-right: 65px;
  background-image: url('images/arrow.svg');
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: 85%;
  &:hover {
    background-color: $orange-hover;
    border-color: $orange-hover;
  }
  &:focus {
    border-color: $orange;
  }
}

.award {
  max-width: 625px;
  margin-bottom: 26px;
  .swiper-button-disabled {
    opacity: .8;
  }
  .swiper-button-lock {
    display: none;
  }
}

.awards-list {
  margin-top: 46px;
  margin-bottom: 35px;
  display: flex;
}

.awards-item {
  @include phone {
    width: auto !important;
  }
}

.awards-logo {
  img {
    height: 91px;
    width: 91px;
    object-fit: contain;
    @include phone {
      width: auto;
      height: 69px;
    }
  }
}

.awards-button-next,
.awards-button-prev {
  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
  color: white;
  font-size: 21px;
}

.awards-button-next {
  right: 42%;
  left: auto;
}

.awards-button-prev {
  right: auto;
  left: 42%;
  transform: rotate(180deg);
}